import React from "react";
import styled from "styled-components";
import { connect } from "react-redux";
import { logout } from "../redux/asyncActions";
import { closePopup } from "../redux/actions";
import Dropdown from "./Dropdown";

const CoverIcon = styled.div`
  position: fixed;
  top: 8px;
  right: 8px;
  width: 30px;
  height: 30px;
`;

const DropdownContainer = styled.div`
  position: fixed;
  right: 8px;
  top: 50px;
`;

const AppDropdown = props => {
  return (
    <DropdownContainer>
      <Dropdown
        items={[
          {
            text: "Not yet",
            onClick() {}
          }
        ]}
      >
        <CoverIcon onClick={props.closePopup} />
      </Dropdown>
    </DropdownContainer>
  );
};

const mapStateToProps = state => {
  return {
    popupComponent: state.app.popupComponent,
    isPopupShown: state.app.isPopupShown
  };
};

const mapDispatchToProps = dispatch => {
  return {
    closePopup: () => {
      dispatch(closePopup());
    }
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(AppDropdown);
