import markdownIt from "markdown-it";
import markdownKatex from "markdown-it-katex";
import markdownCheckbox from "markdown-it-checkbox";
import html2canvas from "html2canvas";
import hljs from "highlightjs";
import config from "../config";

const md = markdownIt({
  html: true, // Enable HTML tags in source
  xhtmlOut: true, // Use '/' to close single tags (<br />).
  // This is only for full CommonMark compatibility.
  breaks: true, // Convert '\n' in paragraphs into <br>
  langPrefix: "language-", // CSS language prefix for fenced blocks. Can be
  // useful for external highlighters.
  linkify: true, // Autoconvert URL-like text to links

  // Enable some language-neutral replacement + quotes beautification
  typographer: true,
  highlight: function(str, lang) {
    if (lang && hljs.getLanguage(lang)) {
      try {
        return hljs.highlight(lang, str).value;
      } catch (__) {}
    }

    return ""; // use external default escaping
  }
});

md.use(markdownKatex).use(markdownCheckbox);

export default function mdToCanvas(markdown, callback) {
  const rendered = md.render(markdown);
  const parser = new DOMParser();
  const wrapper = parser.parseFromString(rendered, "text/html");
  const nodes = wrapper.body.childNodes;
  const markdownBody = document.createElement("div");
  markdownBody.setAttribute("class", "markdown-body");
  nodes.forEach(node => {
    markdownBody.append(node);
  });
  const invisible = document.createElement("div");
  invisible.setAttribute(
    "style",
    "position: fixed; left: -100%; width: 600px;"
  );
  invisible.appendChild(markdownBody);
  document.body.appendChild(invisible);
  html2canvas(markdownBody, {
    proxy: `${config.apiUrl}/corsProxy`,
    useCORS: true,
    scale: 1.3,
    logging: false
  }).then(canvas => {
    callback(canvas);
    invisible.remove();
  });
}
