import React from "react";
import styled from "styled-components";
import { connect } from "react-redux";
import { showPopup } from "../redux/actions";
import AppDropdown from "./AppDropdown";

const Header = styled.div`
  height: 26px;
  line-height: 26px;
  padding: 8px;
  text-align: center;
  font-weight: bold;
  font-size: 12px;
  border-bottom: 1px solid #e6ecf0;
  display: grid;
  grid-template-columns: 1fr 26px 1fr;
  grid-template-rows: 1fr;
`;

const Brand = styled.img`
  height: 26px;
  user-select: none;
  pointer-events: none;
  grid-row: 1;
  grid-column: 2;
`;

const LeftSpace = styled.div`
  grid-row: 1;
  grid-column: 1;
`;

const RightSpace = styled.div`
  grid-row: 1;
  grid-column: 3;

  display: flex;
  justify-content: flex-end;
`;

const IconItem = styled.i`
  font-size: 18px;
  line-height: 26px !important;
  padding: 0px 4px;
  cursor: pointer;
`;

const AppHeader = ({ showPopup }) => {
  return (
    <Header>
      <LeftSpace />
      <Brand src="/icon128.png" />
      <RightSpace>
        <IconItem className="fas fa-cat" onClick={() => showPopup()} />
      </RightSpace>
    </Header>
  );
};

const mapStateToProps = state => {
  return {};
};

const mapDispatchToProps = dispatch => {
  return {
    showPopup: () => {
      dispatch(showPopup(<AppDropdown />));
    }
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(AppHeader);
