import React from "react";
import { connect } from "react-redux";
import { closePopup } from "../redux/actions";
import styled from "styled-components";
import ClickOutside from "react-click-outside";

const Box = styled.div`
  width: 100px;
  height: auto;
  padding-top: 4px;
  padding-bottom: 4px;
  background: white;
  border-radius: 4px;
  border: 1px solid #e6ecf0;
  box-shadow: 0px 0px 4px 0px rgba(0, 0, 0, 0.1);
`;

const List = styled.ul`
  padding: 0px;
  margin: 4px 0px;
`;

const Item = styled.li`
  list-style: none;
  padding: 4px 8px;
  cursor: pointer;
  font-size: 12px;
  font-weight: bold;

  &:hover {
    background: rgba(240, 240, 240, 1);
  }
`;

const Dropdown = props => {
  const items = props.items || [];
  return (
    <ClickOutside onClickOutside={() => props.closePopup()}>
      <Box>
        <List>
          {items.map(item => (
            <Item onClick={e => item.onClick(e)} key={item.text}>
              {item.text}
            </Item>
          ))}
        </List>
        {props.children}
      </Box>
    </ClickOutside>
  );
};

const mapStateToProps = state => {
  return {
    popupComponent: state.app.popupComponent,
    isPopupShown: state.app.isPopupShown
  };
};

const mapDispatchToProps = dispatch => {
  return {
    closePopup: () => {
      dispatch(closePopup());
    }
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Dropdown);
