import React from "react";
import { connect } from "react-redux";
import * as asyncActions from "../redux/asyncActions";
import { showPopup } from "../redux/actions";
import AccountDropdown from "./AccountDropdown";

const AppFooter = ({
  previewMode,
  isLogined,
  isValid,
  isWaitingTweet,
  isWaitingLogin,
  profileImage,
  togglePreview,
  savePNG,
  tweet,
  login,
  showPopup
}) => {
  return (
    <div className="app-footer">
      <button
        className={"btn " + (previewMode ? "" : "btn-outline")}
        onClick={() => togglePreview()}
      >
        Preview
      </button>
      <div className="flex-pad" />
      <button
        className={`btn ${isValid ? "" : "btn-disabled"}`}
        onClick={() => savePNG()}
        disabled={!isValid}
      >
        Save as
      </button>
      {isLogined ? (
        <>
          <button
            className={`btn ${isValid ? "" : "btn-disabled"} ${
              isWaitingTweet ? "btn-wait" : ""
            }`}
            onClick={() => tweet()}
            disabled={!isValid || isWaitingTweet}
          >
            {isWaitingTweet ? "Tweeting" : "Tweet"}
          </button>
          <img
            className="profile-image"
            src={profileImage}
            onClick={() => showPopup()}
          />
        </>
      ) : (
        <button className="btn" onClick={() => !isWaitingLogin && login()}>
          {isWaitingLogin ? "Checking" : "Login"}
        </button>
      )}
    </div>
  );
};

const mapStateToProps = state => {
  return {
    isWaitingTweet: state.app.isWaitingTweet,
    isWaitingLogin: state.app.isWaitingLogin,
    isValid: state.app.isValid,
    previewMode: state.app.previewMode,
    isLogined: state.app.isLogined,
    profileImage: state.app.profileImage
  };
};

const mapDispatchToProps = dispatch => {
  return {
    showPopup: () => {
      dispatch(showPopup(<AccountDropdown />));
    },
    tweet: () => {
      dispatch(asyncActions.tweet());
    },
    togglePreview: () => {
      dispatch(asyncActions.togglePreview());
    },
    savePNG: () => {
      dispatch(asyncActions.saveAs());
    },
    login: () => {
      dispatch(asyncActions.login());
    }
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(AppFooter);
