import React from "react";
import styled from "styled-components";
import { connect } from "react-redux";
import { logout } from "../redux/asyncActions";
import { closePopup } from "../redux/actions";
import Dropdown from "./Dropdown";

const CoverIcon = styled.div`
  position: fixed;
  bottom: 8px;
  right: 8px;
  width: 30px;
  height: 30px;
`;

const DropdownContainer = styled.div`
  position: fixed;
  right: 8px;
  bottom: 50px;
`;

const AccountDropdown = props => {
  return (
    <DropdownContainer>
      <Dropdown
        items={[
          {
            text: "Logout",
            onClick() {
              props.logout();
            }
          }
        ]}
      >
        <CoverIcon onClick={props.closePopup} />
      </Dropdown>
    </DropdownContainer>
  );
};

const mapStateToProps = state => {
  return {
    popupComponent: state.app.popupComponent,
    isPopupShown: state.app.isPopupShown
  };
};

const mapDispatchToProps = dispatch => {
  return {
    closePopup: () => {
      dispatch(closePopup());
    },
    logout: () => {
      dispatch(logout());
    }
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(AccountDropdown);
