import React from "react";
import { UnControlled as CodeMirror } from "react-codemirror2";
import { connect } from "react-redux";
import { setMdTempText } from "../redux/actions";

/**
 * @param {onChange} (value) => {}
 */
const AppBody = ({ initialText, previewMode, previewUrl, onChange }) => {
  return previewMode ? (
    <div className="preview">
      {previewUrl ? (
        <img className="preview-img" src={previewUrl} />
      ) : (
        <div className="preview-loading">
          <div className="lds-ellipsis">
            <div />
            <div />
            <div />
            <div />
          </div>
        </div>
      )}
    </div>
  ) : (
    <div>
      <CodeMirror
        value={initialText}
        options={{
          mode: "gfm",
          theme: "github-light",
          lineNumbers: true,
          lineWrapping: true,
          indentWithTabs: false,
          tabSize: 2
        }}
        editorDidMount={editor => {
          editor.setOption("extraKeys", {
            Tab: cm => {
              const spaces = Array(cm.getOption("indentUnit") + 1).join(" ");
              cm.replaceSelection(spaces);
            }
          });
        }}
        onChange={(editor, data, value) => onChange(value)}
      />
    </div>
  );
};

const mapStateToProps = state => {
  return {
    previewMode: state.app.previewMode,
    previewUrl: state.app.previewUrl,
    initialText: state.app.mdText
  };
};

const mapDispatchToProps = dispatch => {
  return {
    onChange: value => {
      dispatch(setMdTempText(value));
    }
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(AppBody);
