/**
 * action types
 */

export const TOGGLE_PREVIEW_MODE = "TOGGLE_PREVIEW_MODE";
export const SET_PREWVIEW_URL = "SET_PREWVIEW_URL";
export const COMMIT_MD_TEMP_TEXT = "COMMIT_MD_TEMP_TEXT";
export const SET_MD_TEMP_TEXT = "SET_MD_TEMP_TEXT";
export const SET_ACCOUNT = "SET_ACCOUNT";
export const SET_WAITING_TWEET = "SET_WAITING_TWEET";
export const SET_WAITING_LOGIN = "SET_WAITING_LOGIN";

export const SHOW_POPUP = "SHOW_POPUP";
export const CLOSE_POPUP = "CLOSE_POPUP";

/**
 * action creaters
 */
export function togglePreviewMode() {
  return { type: TOGGLE_PREVIEW_MODE };
}

export function setPreviewUrl(url) {
  return { type: SET_PREWVIEW_URL, url };
}

export function commitMdTempText() {
  return { type: COMMIT_MD_TEMP_TEXT };
}

export function setMdTempText(text) {
  return { type: SET_MD_TEMP_TEXT, text };
}

export function setAccount({ isLogined, profileImage }) {
  return { type: SET_ACCOUNT, isLogined, profileImage };
}

export function setWaitingTweet(value) {
  return { type: SET_WAITING_TWEET, value };
}

export function setWaitingLogin(value) {
  return { type: SET_WAITING_LOGIN, value };
}

export function showPopup(popupComponent) {
  return {
    type: SHOW_POPUP,
    popupComponent
  };
}

export function closePopup() {
  return { type: CLOSE_POPUP };
}
