import { combineReducers } from "redux";
import * as ACTIONS from "./actions";

const INITIAL_TEXT = "# Title";
const validator = text => {
  return text.length > 0;
};

const appInitialState = {
  previewMode: false,
  previewUrl: "",
  mdText: INITIAL_TEXT,
  mdTempText: INITIAL_TEXT,
  isValid: validator(INITIAL_TEXT),
  isWaitingTweet: false,
  isWaitingLogin: true,
  isLogined: false,
  profileImage: "",
  popupComponent: null,
  isPopupShown: false
};

function app(state = appInitialState, action) {
  switch (action.type) {
    case ACTIONS.TOGGLE_PREVIEW_MODE:
      const previewMode = state.previewMode;
      return {
        ...state,
        previewMode: !previewMode
      };
    case ACTIONS.SET_PREWVIEW_URL:
      return {
        ...state,
        previewUrl: action.url
      };
    case ACTIONS.COMMIT_MD_TEMP_TEXT:
      return {
        ...state,
        mdText: state.mdTempText
      };
    case ACTIONS.SET_MD_TEMP_TEXT:
      return {
        ...state,
        mdTempText: action.text,
        isValid: validator(action.text)
      };
    case ACTIONS.SET_ACCOUNT:
      return {
        ...state,
        isLogined: action.isLogined,
        profileImage: action.profileImage
      };
    case ACTIONS.SET_WAITING_TWEET:
      return {
        ...state,
        isWaitingTweet: action.value
      };
    case ACTIONS.SET_WAITING_LOGIN:
      return {
        ...state,
        isWaitingLogin: action.value
      };
    case ACTIONS.SHOW_POPUP:
      return {
        ...state,
        isPopupShown: true,
        popupComponent: action.popupComponent
      };
    case ACTIONS.CLOSE_POPUP:
      return {
        ...state,
        isPopupShown: false,
        popupComponent: null
      };
    default:
      return state;
  }
}

const rootReducer = combineReducers({
  app
});

export default rootReducer;
